@import './../../../scss/global.scss';

.link {
  font-size: 36px;
  font-family: 'Regular';
  margin-bottom: 28px;
  cursor: pointer;
  position: relative;
  display: inline-block;
  transition: 0.5s;
  @include devices(mobile) {
    font-size: 28px;
  }
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    left: 0;
    top: 100%;
    height: 3px;
    background-color: $color-talent;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s linear;
  }
  &:hover:after {
    transform-origin: bottom left;
    transform: scaleX(1);
  }
  &:hover {
    color: $color-talent;
  }
}
